* {
  padding: 0;
  margin: 0;
}
.App {
  width: 100wv;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(26, 162, 236);
  background: linear-gradient(
    90deg,
    rgba(26, 162, 236, 1) 0%,
    rgba(26, 167, 236, 1) 50%,
    rgba(26, 185, 236, 1) 100%
  );
}

.App h1 {
  font-size: 40px;
  margin-top: 20px;
  margin-bottom: 20px;
  color: antiquewhite;
  font-family: Arial, sans-serif;
  letter-spacing: 0.5em;
  -webkit-text-stroke: 2px #212121;
}

.board {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

@media (max-width: 500px) {
  .App h1 {
    font-size: 20px;
    padding: 0;
    margin-bottom: 30px;
  }

  .board {
    width: 300px;
    height: 300px;
  }
}

.row {
  display: flex;
  flex-direction: row;
  flex: 33%;
  border-radius: 10px;
}

.square {
  border-radius: 10px;
  width: 33%;
  height: 100%;
  border: 1px solid #212121;

  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 120px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  color: antiquewhite;
  text-shadow: 4px 4px 6px rgba(66, 68, 90, 1);
  -webkit-text-stroke: 2px #212121;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

@media (max-width: 500px) {
  .square {
    font-size: 40px;
  }
}

.square:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.square:active {
  background-color: #d1d1d1;
}
